import { css } from 'lit';

export const styles = css`[part=title]::slotted(*){font-family:var(--igc-subtitle-1-font-family);font-weight:var(--igc-subtitle-1-font-weight);font-size:var(--igc-subtitle-1-font-size);font-style:var(--igc-subtitle-1-font-style);letter-spacing:var(--igc-subtitle-1-letter-spacing);text-transform:var(--igc-subtitle-1-text-transform);line-height:var(--igc-subtitle-1-line-height);margin-top:var(--igc-subtitle-1-margin-top);margin-bottom:var(--igc-subtitle-1-margin-bottom)}[part=subtitle]::slotted(*){font-family:var(--igc-body-2-font-family);font-weight:var(--igc-body-2-font-weight);font-size:var(--igc-body-2-font-size);font-style:var(--igc-body-2-font-style);letter-spacing:var(--igc-body-2-letter-spacing);text-transform:var(--igc-body-2-text-transform);line-height:var(--igc-body-2-line-height);margin-top:var(--igc-body-2-margin-top);margin-bottom:var(--igc-body-2-margin-bottom)}:host([open]) [part~=content]{font-family:var(--igc-caption-font-family);font-weight:var(--igc-caption-font-weight);font-size:var(--igc-caption-font-size);font-style:var(--igc-caption-font-style);letter-spacing:var(--igc-caption-letter-spacing);text-transform:var(--igc-caption-text-transform);line-height:var(--igc-caption-line-height);margin-top:var(--igc-caption-margin-top);margin-bottom:var(--igc-caption-margin-bottom);color:hsla(var(--igc-gray-700), var(--igc-gray-a))}:host([disabled]) [part~=indicator],:host([disabled]) [part=title],:host([disabled]) [part=subtitle]{color:hsla(var(--igc-gray-400), var(--igc-gray-a))}`;
